
import common from '@/libs/common.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import FileItem from 'c%/FileItem.vue'
import Comment from 'c%/comment.vue'
import Group from 'vux/src/components/group/index.vue'


export default{
mixins: [ common, wxjssdk ],
components: {
Group,
Comment,
FileItem
},
data () {
return {
id: this.$route.params.id,
info: {
student: {}
}
}
},
mounted () {
this.init_data()
},
methods: {
setShare () {
let [title,img,desc] = [this.info.art_name,'',this.info.art_desc]

if(this.info.student_artwork_attachment.length){
img = this.info.student_artwork_attachment[0].file_url
}

this.setWxShare(title,img,desc)
},
formatDate (time) {
time = time||''
let result = time.substring(5,10)
return result
},
init_data () {
let id = this.id

this.$rest('/sapi/share/student_artwork_detail?with=student&sart_id='+id).get()
.success(data=>{
this.info = data
this.setShare()
this.loading = true
}).error(response=>{
this.loading = true
this.toast(response.body.message||'加载错误')
})
}
}
}



